<template>
    
    <div class="container">
        <div class="row">
            <div class="col-12 mb-5 card mt-5 shadow">
                <div class="card-body">
                    <h3>Ürün Listesi</h3>
                    <hr>
                    <table class="table table-hover table-striped table-bordered" v-if="getProducts.length > 0">
                        <thead>
                        <th>id</th>
                        <th>Ürün Adı</th>
                        <th>Adet</th>
                        <th>Fiyat</th>
                        <th>Açıklama</th>
                        </thead>
                        <tbody>
                        <tr v-for="product in getProducts">
                            <td class="align-middle text-center"><span class="badge badge-info"> {{ product.key }} </span></td>
                            <td class="align-middle text-center"> {{ product.title }} </td>
                            <td class="align-middle text-center"> {{ product.count }} </td>
                            <td class="text-center align-middle" :class="getCountClasses(product.count)"> {{ product.price | currency}}</td>
                            <td class="align-middle"> {{ product.description }}</td>
                        </tr>
                        </tbody>
                    </table>
                    <div class="alert alert-warning" v-else>
                        <strong>Henüz Burada Bir Kayıt Bulamadık</strong>
                        <br>
                        <small>Kayıt Eklemek için Ürün İşlemleri menüsünden yararlanabilirsiniz
                        </small>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from "vuex"
export default{
    computed:{
        ...mapGetters(["getProducts"]),
    },
    methods:{
        getCountClasses(count){
            return{
              'btn-danger text-white' : count == 0 || count == null, 
              'btn-success text-white' : count > 0 
            }
        }
    }
}
</script>
<style>
    .badge-info{
        background: red;
    }
    .btn-success {
    color: #fff;
    background-color: #198754 !important;
    border-color: #198754 !important;
}
.btn-danger {
    color: #fff;
    background-color: #dc3545 !important;
    border-color: #dc3545 !important;
}
</style>



